<template>
  <div class="wrapper">
    <div class="animated fadeIn">
      <b-card header=" ">
        <b-form>
          <b-btn class="mb-3" variant="primary"> </b-btn>
          <b-row>
            <b-col lg="2">
              <b-form-group label=" ">
                <b-form-select v-model="checklistTypeSelected" :options="checkListTypes" @change="onChecklistTypeChange"></b-form-select>
              </b-form-group>
            </b-col>
            <b-col>
              <b-form-group label="">
                <b-form-input placeholder=" "></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-form-group label=" ">
            <input-tag placeholder="" :tags="sampleAnswers"></input-tag>
            <b-btn variant="link" size="sm" @click="reversSampleAnswers"></b-btn>
          </b-form-group>
          <b-form-group label="">
            <quill-editor :options="editorOption"></quill-editor>
          </b-form-group>
        </b-form>

        <!--   -->
        <b-row>
          <b-col lg="6">
            <b-card header=" ">
              <b-tabs pills>
                <b-tab title=" " active>
                  <b-form>
                    <b-form-group label="" horizontal>
                      <v-select label="name" placeholder=""></v-select>
                    </b-form-group>
                    <b-form-group label="" horizontal>
                      <v-select label="name" placeholder=""></v-select>
                    </b-form-group>
                    <b-form-group label="" horizontal>
                      <v-select label="name" placeholder=""></v-select>
                    </b-form-group>
                    <b-form-group label="">
                      <b-form-input type="text" required placeholder=" " v-model="form.question">
                      </b-form-input>
                    </b-form-group>
                    <b-form-group label="">
                      <quill-editor :options="editorOption"></quill-editor>
                    </b-form-group>
                    <b-form-group label="">
                      <quill-editor :options="editorOption"></quill-editor>
                    </b-form-group>
                    <div class="d-flex justify-content-between">
                      <b-form-checkbox id="checkbox1"
                        value="accepted"
                        unchecked-value="not_accepted">
                          
                      </b-form-checkbox>
                      <b-btn variant="primary"></b-btn>
                    </div>
                  </b-form>
                </b-tab>
                <b-tab title=" ">
                  <b-form-group label="">
                    <b-form-input type="text" required placeholder=" " v-model="form.question">
                    </b-form-input>
                  </b-form-group>
                  <b-form-group label="">
                    <quill-editor :options="editorOption"></quill-editor>
                  </b-form-group>
                  <div class="d-flex justify-content-between">
                    <b-form-checkbox id="checkbox1"
                      value="accepted"
                      unchecked-value="not_accepted">
                        
                    </b-form-checkbox>
                    <b-btn mr-auto variant="primary"></b-btn>
                  </div>
                </b-tab>
              </b-tabs>
            </b-card>
          </b-col>
          <b-col>
            <b-card header=" " >
              <!--<b-table striped hover responsive :items="itemList"></b-table>-->
            <draggable v-model="itemList">
              <transition-group name="no" class="list-group">
                <a href="#" @click.prevent="" class="list-group-item list-group-item-action d-flex justify-content-between align-items-center" role="tab" v-for="(item, index) in itemList" :key="item.id">
                  {{index + 1}}. {{item.name}}
                  <b-btn variant="danger"></b-btn>
                </a>
              </transition-group>
            </draggable>
            </b-card>
          </b-col>
        </b-row>        
      </b-card>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { quillEditor } from 'vue-quill-editor'
import draggable from 'vuedraggable'

export default {
  name: 'checklist-detail',

  components: {
    quillEditor,
    draggable
  },

  data () {
    return {
      checklistTypeSelected: null,
      checklistQuizTypeSelected: null,
      checklistQuizTypes: [
        { text: '', value: 'S' },
        { text: '', value: 'T' },
        { text: '', value: 'M' }
      ],
      form: {
        question: null
      },
      sampleAnswers: [],
      itemList: [
        { id: 1, name: 'Macdonald1' },
        { id: 2, name: 'Macdonald2' },
        { id: 3, name: 'Macdonald3' },
        { id: 4, name: 'Macdonald4' }
      ]
    }
  },

  methods: {
    reversSampleAnswers () {
      this.sampleAnswers.reverse()
    },

    onChecklistTypeChange (selected) {
      switch (selected) {
        case 'supervisor':
          this.sampleAnswers = [ '1', '0' ]
          break
        case 'shopper':
          this.sampleAnswers = [ '7', '6', '5', '4', '3', '2', '1' ]
          break
        case 'agent':
          this.sampleAnswers = [ '5', '10' ]
          break
        default:
          break
      }
    }
  },

  computed: {
    ...mapGetters({
      checkListTypes: 'checkListTypes',
      editorOption: 'editorOption'
    })
  }
}
</script>


